import styles from "./Services.module.scss";
import Slider from "react-slick";

import { Development } from "./Slides/Development/Development";
import { Context } from "./Slides/Context/Context";
import { Seo } from "./Slides/Seo/Seo";
import { Target } from "./Slides/Target/Target";

import { useRef } from "react";

export const Services = (props) => {
  const settings = {
    dots: true,
    arrows: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
    pauseOnHover: true,
    // dotsClass: "dots",
    pauseOnDotsHover: true,
    adaptiveHeight: true,
  };

  const sliderRef = useRef();

  const handleOnClick = (index) => {
    sliderRef.current.slickGoTo(index);
  };

  return (
    <section ref={props.servicesRef} className={styles.services}>
      <div className={styles.wrapper}>
        <div onClick={() => handleOnClick(0)}>1</div>
        <div onClick={() => handleOnClick(1)}>2</div>
        <div onClick={() => handleOnClick(2)}>3</div>
        <div onClick={() => handleOnClick(3)}>4</div>
        <h2 className={styles.title}>НАШИ УСЛУГИ</h2>
        <Slider {...settings} ref={sliderRef}>
          <Development />
          <Context />
          <Seo />
          <Target />
        </Slider>
      </div>
    </section>
  );
};
